
import { computed, defineComponent, onMounted, reactive, toRefs } from "vue";
export default defineComponent({
  name: "BnMessage",
  props: {
    type: {
      type: String,
      default: "success",
    },
    message: {
      type: String,
    },
  },
  setup(props) {
    let classes = computed(() => {
      return `bn-message-${props.type}`;
    });
    
    const state = reactive({});

    onMounted(() => {});

    return {
      ...toRefs(state),
      classes,
    };
  },
});
